const moneyDwInt = v => {
	if(!v) return 0
	return Number(String(v).split('.')[0])
}
const moneyDwPoint = v => {
	if(!v) return '00'
	return String(v.toFixed(2)).split('.')[1]
}
const wxBillState = v => {
	if(v == 0) return ''
	if(v == 1) return ''
	return v
}
const toFixed2 = v => {
	if(!v) v = 0
	return v.toFixed(2)
}

//
const timeFilter = v => {
	if(!v) return null
	let dateArr = v.split(' ')[0].split('-')
	let timeArr = v.split(' ')[1].split(':')
	let arr = [...dateArr, ...timeArr]
	arr = arr.map(ele => +ele)
	// console.log(arr)
	let now = new Date().toLocaleString()
	let nowArr1 = now.split(' ')[0].split('/')
	let nowArr2 = now.split(' ')[1].slice(2).split(':')
	let nowArr = [...nowArr1, ...nowArr2]
	nowArr = nowArr.map(ele => +ele)
	if(now.indexOf('') != -1) {
		nowArr[3] = nowArr[3] + 12
	}
	// console.log(nowArr)
	
	if(nowArr[0] == arr[0] && nowArr[1] == arr[1] && nowArr[2] - arr[2] <= 1) {
		if(nowArr[2] == arr[2]) {
			if(nowArr[3] == arr[3]) {
				return nowArr[4] - arr[4] + 1 + ''
			}
			else if(nowArr[4] < arr[4] && nowArr[3] - arr[3] == 1){
				return nowArr[4] - arr[4] + 61 + ''
			}
			return nowArr[3] - arr[3] + ''
			
		}else if(nowArr[2] - arr[2] == 1 && nowArr[3] - arr[3] < 0) {
			return (+nowArr[3]) - (+arr[3]) + 24 + ''
		}else {
			return v.split(' ')[0].slice(5)
		}
	} 
	if(nowArr[0] != arr[0]) {
		return v.split(' ')[0]
	}
	return v.split(' ')[0].slice(5)
}
export default {
	orderStatus,
	timeFilter,
	toTools,
	toState,
	toPayState,
	toPayType,
	toTranState,
	moneyDwInt,
	moneyDwPoint,
	wxBillState,
	toFixed2,
	buyGetType
}