import Request from '@/utils/luch-request/index.js'
import md5Libs from "uview-ui/libs/function/md5";

// storagetoken
const getTokenStorage = () => {
	let token = ''
	try {
		token = uni.getStorageSync('userid')
	} catch (e) {
		//TODO handle the exception
	}
	return token
}

const http = new Request()

const extConfig = uni.getExtConfigSync ? uni.getExtConfigSync():{}
console.log(extConfig)
// console.log(extConfig.attr.login)
			
uni.setStorageSync('xcx_login', extConfig.attr.login);
uni.setStorageSync('xcx_appid', extConfig.attr.wxappid);
 
function get_xcx_code() {
	return new Promise((resolve, rejected) => {
		uni.login({
			success: function (res){
				resolve(res.code);
			},
			fail: err => {
				md5flag = true
				rejected(err)
			}
		});
	});
}

async function refreshToken() {
	// tokentoken
	try{
		let code = await get_xcx_code();
		console.log('code:',code)
		return http.post('Xcx/xcx_login',{code:code})
	}catch(e){
		return e
	}
	
}


// setTokentokenheadertokenlocalStorage
http.setToken = (token, md5flag) => {
	http.config.header['userid'] = token
	if(!md5flag) {
		// md5userid
		uni.setStorageSync('userid', token) 
	}
	
}
let md5flag = false
let requests = [] // token
let isRefreshing = false //token


http.setConfig((config) => { /*  */
	config.baseURL = 'https://wx3.y.netsun.com/' /*  */
	config.header = {
		...config.header,
		'content-type': 'application/x-www-form-urlencoded',
		'appid': 10000,
		'appsecret': '7923FoGlaAlRnbpfl+lepwzh/2lVLDAnb8gyRDSpJKX6TJLW9CTXfW4',
		'xcxlogin': extConfig.attr.login,
		'xcxappid': extConfig.attr.wxappid,
		'market': extConfig.attr.market
	}
	return config
})


http.interceptors.request.use((config) => { /* async await  */
	const token = getTokenStorage()
	config.header = {
		...config.header,
		'userid': token
	}
	// token
	if (config.url.indexOf('xcx_login') >= 0) {
		return config
	}
	if (!token) {
		// token
		if (!isRefreshing) {
			console.log('token ing')
			isRefreshing = true
			refreshToken().then(res => {
				console.log('token',res)
				let userid = ""
				if(res.errMsg != "request:ok") {
					userid = md5Libs.md5(formatDate(new Date()) + 'toocle')
					http.setToken(userid, true)
				}else {
					userid = res.data.userid
					http.setToken(userid)
				}
				
				console.log('token')
				requests.forEach(cb => cb(userid))
				// 
				requests = []
			}).catch(res => {
				console.error('refresh token error: ', res)
			}).finally(() => {
				isRefreshing = false
			})
		}
		return new Promise(resolve => {
			requests.push((token) => {
				// configtokentokentoken
				config.header['userid'] = token
				resolve(config)
			})
		})

	}
	/*
	 if (!token) { // tokenreturn Promise.reject(config) 
	   return Promise.reject(config)
	 }
	 */
	return config
}, (config) => {
	return Promise.reject(config)
})


http.interceptors.response.use(async (response) => { /* async await   */
	// if (response.data.code !== 200) { // 200reject()
	//   return Promise.reject(response)
	// }
	uni.hideLoading()
	uni.hideNavigationBarLoading();
	if (response.hasOwnProperty('data')) {
		if (response.data.code != 1) {
			if (response.data.msg) {
				uni.showToast({
					title: response.data.msg,
					icon: 'none',
					mask: true,
					duration: 1000
				})
			}
		}
	} 
	return response
}, (response) => { // async await 
	console.log(response)
	return Promise.reject(response)
})
var formatDate = function (date) { 
    var y = date.getFullYear(); 
    var m = date.getMonth() + 1; 
    m = m < 10 ? '0' + m : m; 
    var d = date.getDate(); 
    d = d < 10 ? ('0' + d) : d; 
    return y + m + d; 
}; 
